.main-container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .logo-container {
    width: 100%;
    text-align: center;
    margin-bottom: 20px;
  }
  
  .flutter-logo {
    max-width: 250px;
    height: auto;
  }
  
  .content-container {
    flex: 1;
    width: 100%;
  }
  
  .project-image {
    max-width: 50%;
    height: auto;
    display: block;
    margin: 20px auto;
  }
