.main-container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .logo-container {
    width: 100%;
    text-align: center;
    margin-bottom: 20px;
  }
  
  .java-logo {
    max-width: 250px; /* 로고의 최대 너비 설정 */
    height: auto; /* 로고의 높이가 자동으로 조정되도록 설정 */
  }
  
  .content-container {
    flex: 1;
    width: 100%; /* 컨텐츠 컨테이너의 너비를 100%로 설정 */
  }
  
  .project-image {
    max-width: 50%; /* 이미지의 최대 너비를 부모의 50%로 설정 */
    height: auto; /* 이미지의 높이가 자동으로 조정되도록 설정 */
    display: block; /* 이미지를 블록 요소로 설정 */
    margin: 20px auto; /* 이미지를 가운데 정렬 */
  }
