.resume-container {
    font-family: 'Arial', sans-serif;
    max-width: 800px;
    margin: 20px auto;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 8px;
    background-color: #f9f9f9;
  }
  
  .resume-container h3,
  .resume-container em {
    text-align: center;
    margin: 20px 0;
  }
  
  .resume-container table {
    width: 100%;
    border-collapse: collapse;
    margin-bottom: 20px;
  }
  
  .resume-container td,
  .resume-container th {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: left;
  }
  
  .resume-container th {
    background-color: #e3fcff;
  }
  
  .resume-container tr:nth-child(even) {
    background-color: #f9f9f9;
  }
  
  .resume-container td[rowspan] {
    text-align: center;
    vertical-align: middle;
  }
  
  #pledge {
    text-align: center;
    margin-top: 30px;
    font-style: italic;
  }
  
  .resume-container th[bgcolor="#e3fcff"] {
    background-color: #e3fcff;
  }
  
  .resume-container td:empty {
    background-color: #fff;
  }
  
  .resume-container th[bgcolor="#e3fcff"],
  .resume-container td[colspan="3"] {
    background-color: #e3fcff;
  }
  
  .resume-container td[bgcolor="#e3fcff"] {
    text-align: center;
  }
  
  .resume-container td[rowspan] {
    width: 120px;
    height: 160px;
  }
  
  #pledge {
    font-weight: bold;
    margin-top: 20px;
    padding-top: 10px;
    border-top: 1px solid #ddd;
  }
  