.login-container {
    background-color: #f5f5dc;
    padding: 20px;
    border-radius: 10px;
    width: 50%;
    min-width: 300px;
    margin: auto;
    margin-top: 5px;
    margin-bottom: 5px;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  }
  
  .login-title {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .login-table {
    width: 100%;
    margin-bottom: 20px;
  }
  
  .login-password-display {
    text-align: left;
    margin-left: 10px;
  }
  
  input[type="password"] {
    width: 70%;
    padding: 10px;
    margin-bottom: 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
  }
  